.skills {
	padding: 20px;
}

.skills h2 {
	font-size: 2em;
	margin-bottom: 20px;
}

.skills ul {
	list-style-type: none;
	padding: 0;
}

.skills li {
	display: inline-block;
	margin: 10px;
	padding: 10px 20px;
	border: 1px solid #ddd;
	border-radius: 20px;
	background-color: #f7f7f7;
	font-size: 1.2em;
}
