.projects {
	padding: 20px;
}

.projects h2 {
	font-size: 2em;
	margin-bottom: 20px;
}

.project-list {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
}

.project {
	margin: 10px;
	padding: 20px;
	border: 1px solid #ddd;
	border-radius: 8px;
	width: 30%;
	box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.project img {
	width: 100%;
	height: auto;
	border-radius: 4px;
}

.project h3 {
	font-size: 1.5em;
	margin: 10px 0;
}

.project p {
	font-size: 1em;
}
