.experience {
	padding: 20px;
	margin-bottom: 5%;
}

.experience h2 {
	font-size: 2em;
	margin-bottom: 20px;
}

.experience ul {
	list-style-type: none;
	padding: 0;
}

.experience li {
	background-color: #f7f7f7;
	margin: 10px 0;
	padding: 10px;
	border: 1px solid #ddd;
	border-radius: 8px;
	font-size: 1.2em;
}
